@import url("https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600;700&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  min-width: 280px;
}

body {
  min-width: 280px;
  font-family: "Crimson Text", serif;
  color: #e0e0e0;
  background-color: #000000;
  font-size: 18px;
  line-height: 1.5em;
  font-weight: 400;
}
@media (max-width: 575px) {
  body {
    font-size: 14px;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.page {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  max-width: 1230px;
  margin: auto;
  padding: 0 15px;
}

.section__title {
  font-size: 64px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  color: #3CFD5B;
  padding: 20px 15px;
  border-bottom: 1px solid #e0e0e0;
  text-align: center;
}
@media (max-width: 991px) {
  .section__title {
    font-size: 54px;
  }
}
@media (max-width: 767px) {
  .section__title {
    font-size: 34px;
  }
}
@media (max-width: 575px) {
  .section__title {
    font-size: 24px;
  }
}

.header__inner {
  border: 1px #fff solid;
  padding: 20px 30px;
  margin-top: -1px;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
@media (max-width: 575px) {
  .nav__list {
    flex-direction: column;
  }
}

.nav__item:not(:last-child) {
  margin-right: 90px;
}
@media (max-width: 991px) {
  .nav__item:not(:last-child) {
    margin-right: 50px;
  }
}
@media (max-width: 767px) {
  .nav__item:not(:last-child) {
    margin-right: 30px;
    text-align: center;
  }
}
@media (max-width: 575px) {
  .nav__item:not(:last-child) {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .nav__item:nth-child(1) {
    order: 2;
  }
}
@media (max-width: 767px) {
  .nav__item:nth-child(2) {
    order: 3;
  }
}
@media (max-width: 767px) {
  .nav__item:nth-child(3) {
    order: 1;
    flex-grow: 1;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .nav__item:nth-child(4) {
    order: 4;
  }
}
@media (max-width: 767px) {
  .nav__item:nth-child(5) {
    order: 5;
  }
}
@media (max-width: 767px) {
  .nav__item:nth-child(4) {
    order: 5;
  }
}

.nav__link {
  display: inline-block;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #e0e0e0;
}
.nav__link:hover {
  color: #3CFD5B;
}

.logo {
  width: 120px;
  margin-top: 10px;
}
@media (max-width: 767px) {
  .logo {
    width: 100px;
    margin: 0;
  }
}
.logo img {
  width: 100%;
  height: auto;
}

.hero__inner {
  border: 1px #fff solid;
  margin-top: -1px;
  display: flex;
}
@media (max-width: 991px) {
  .hero__inner {
    flex-direction: column;
    align-items: center;
  }
}

.hero__left {
  width: 50%;
  border-right: 1px #fff solid;
}
@media (max-width: 991px) {
  .hero__left {
    border: none;
    width: 100%;
    text-align: center;
  }
}

.hero__img {
  padding: 20px;
}
@media (max-width: 767px) {
  .hero__img {
    padding: 10px;
  }
}

.ticker {
  position: relative;
  overflow: hidden;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
}

.ticker__wrapper {
  display: flex;
  padding: 20px;
}
@media (max-width: 1091px) {
  .ticker__wrapper {
    padding: 15px;
  }
}
@media (max-width: 767px) {
  .ticker__wrapper {
    padding: 10px;
  }
}

.ticker__item {
  font-size: 18px;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  padding: 0 15px;
  white-space: nowrap;
  text-transform: uppercase;
  animation: ticker 5s linear infinite;
}
@media (max-width: 767px) {
  .ticker__item {
    font-size: 16px;
  }
}

.ticker__img {
  margin-left: 30px;
  width: 40px;
}
@media (max-width: 767px) {
  .ticker__img {
    margin-left: 20px;
    width: 30px;
  }
}

@keyframes ticker {
  0% {
    transform: translateZ(0);
  }
  to {
    transform: translate3d(-100%, 0, 0);
  }
}
.hero__right {
  width: 50%;
}
@media (max-width: 1191px) {
  .hero__right {
    width: 100%;
    text-align: center;
  }
}

.hero__title {
  font-size: 100px;
  line-height: 1;
  font-weight: 700;
  text-transform: uppercase;
  color: #3CFD5B;
  padding: 20px 15px;
}
@media (max-width: 1191px) {
  .hero__title {
    font-size: 90px;
  }
}
@media (max-width: 1091px) {
  .hero__title {
    font-size: 80px;
    padding: 20px 15px 10px;
  }
}
@media (max-width: 991px) {
  .hero__title {
    font-size: 72px;
  }
}
@media (max-width: 891px) {
  .hero__title {
    font-size: 64px;
  }
}
@media (max-width: 767px) {
  .hero__title {
    font-size: 54px;
  }
}
@media (max-width: 575px) {
  .hero__title {
    font-size: 42px;
  }
}

.hero__subtitle {
  font-size: 34px;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
  color: #e0e0e0;
  padding: 0 15px 20px;
  border-bottom: 1px solid #e0e0e0;
}
@media (max-width: 1191px) {
  .hero__subtitle {
    font-size: 32px;
  }
}
@media (max-width: 1091px) {
  .hero__subtitle {
    font-size: 28px;
  }
}
@media (max-width: 575px) {
  .hero__subtitle {
    font-size: 20px;
  }
}

.tours__inner {
  border-right: 1px #fff solid;
  border-left: 1px #fff solid;
}

.tours__list {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 991px) {
  .tours__list {
    flex-direction: column;
    align-items: center;
  }
}

.tour__item {
  padding: 20px;
  width: 33.33333%;
  text-align: center;
}
@media (max-width: 991px) {
  .tour__item {
    width: 100%;
  }
}
.tour__item:not(:last-child) {
  border-right: 1px #fff solid;
}
@media (max-width: 991px) {
  .tour__item:not(:last-child) {
    border: none;
  }
}

.card {
  width: 100%;
}

.card__img {
  overflow: hidden;
}
.card__img img {
  transition: transform 0.5s ease;
}
.card__img img:hover {
  transform: scale(1.03);
}

.card__title {
  font-size: 28px;
  font-weight: 600;
  line-height: 1.2;
  text-transform: uppercase;
  color: #e0e0e0;
  text-align: center;
  padding-top: 10px;
}

.card__subtitle {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
  text-transform: uppercase;
  color: #e0e0e0;
  padding: 10px 0 30px;
  text-align: center;
}

.gallery__inner {
  border-right: 1px #fff solid;
  border-left: 1px #fff solid;
  margin-top: -1px;
  padding: 20px;
}
@media (max-width: 767px) {
  .gallery__inner {
    padding: 10px;
  }
}

.slider__wrapper {
  position: relative;
  max-width: 1200px;
  height: 627px;
}

.slider {
  position: relative;
  width: auto;
  height: 627px;
  background-color: #e0e0e0;
  overflow: hidden;
}

.slider__item {
  position: absolute;
  width: 100%;
  height: auto;
  left: 0;
  top: 0;
}
.slider__item img {
  width: 100%;
  height: 627px;
  object-fit: cover;
}

.hidden {
  display: none;
  opacity: 0;
}

.prev,
.next {
  font-size: 54px;
  line-height: 1;
  opacity: 0.7;
  position: absolute;
  bottom: 45%;
  z-index: 1;
  background: transparent;
  border: none;
  color: #e0e0e0;
  padding: 15px;
  cursor: pointer;
}
@media (max-width: 991px) {
  .prev,
  .next {
    font-size: 48px;
  }
}
@media (max-width: 767px) {
  .prev,
  .next {
    font-size: 40px;
  }
}
.prev:hover,
.next:hover {
  color: #3CFD5B;
  opacity: 0.7;
}

.prev {
  left: 30px;
}
@media (max-width: 767px) {
  .prev {
    left: 10px;
  }
}

.next {
  right: 30px;
}
@media (max-width: 767px) {
  .next {
    right: 10px;
  }
}

.about__inner {
  border: 1px #fff solid;
  border-bottom: none;
  margin-top: -1px;
  display: flex;
}
@media (max-width: 991px) {
  .about__inner {
    flex-direction: column;
    align-items: center;
  }
}

.about__left {
  width: 50%;
  border-right: 1px #fff solid;
}
@media (max-width: 991px) {
  .about__left {
    border: none;
    width: 100%;
    text-align: center;
  }
}

.about__img {
  padding: 20px;
}
@media (max-width: 767px) {
  .about__img {
    padding: 10px;
  }
}

.about__right {
  width: 50%;
}
@media (max-width: 991px) {
  .about__right {
    width: 100%;
    text-align: center;
  }
}

.about__title {
  font-size: 36px;
  line-height: 1;
  font-weight: 700;
  text-transform: uppercase;
  color: #3CFD5B;
  padding: 20px 15px 10px;
}

.about__subtitle {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
  text-transform: uppercase;
  color: #e0e0e0;
  padding: 0 15px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.about__text {
  padding: 20px;
  font-size: 24px;
  line-height: 1.5;
  color: #e0e0e0;
}
@media (max-width: 991px) {
  .about__text {
    padding-top: 0;
  }
}
@media (max-width: 757px) {
  .about__text {
    font-size: 16px;
    padding: 0 10px 10px;
  }
}
@media (max-width: 575px) {
  .about__text {
    font-size: 14px;
  }
}

.ticker-bottom__inner {
  border-right: 1px #fff solid;
  border-left: 1px #fff solid;
  margin-top: -1px;
}

.contacts__inner {
  border-right: 1px #fff solid;
  border-left: 1px #fff solid;
  display: flex;
}
@media (max-width: 991px) {
  .contacts__inner {
    flex-direction: column;
    align-items: center;
  }
}

.contacts__letf {
  width: 50%;
  border-right: 1px #fff solid;
}
@media (max-width: 991px) {
  .contacts__letf {
    border: none;
    width: 100%;
    text-align: center;
  }
}
.contacts__letf img {
  padding: 20px;
}
@media (max-width: 767px) {
  .contacts__letf img {
    padding: 10px;
  }
}

.contacts__right {
  width: 50%;
}
@media (max-width: 991px) {
  .contacts__right {
    width: 100%;
    text-align: center;
  }
}

.form {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
}
@media (max-width: 991px) {
  .form {
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .form {
    padding: 10px;
  }
}

.error-message {
  color: #e74c3c;
  font-size: 14px;
  text-align: left;
}

.success-message {
  color: #3CFD5B;
  font-weight: 500;
  font-size: 14px;
  text-align: left;
}

.form__input {
  display: block;
  width: 100%;
  padding: 0 15px;
  height: 50px;
  background: transparent;
  border: 1px solid #e0e0e0;
  margin-bottom: 20px;
  color: #e0e0e0;
}
@media (max-width: 767px) {
  .form__input {
    padding: 10px;
    margin-bottom: 10px;
  }
}
.form__input:focus {
  outline: 1px solid #3CFD5B;
}
.form__input::placeholder {
  color: #e0e0e0;
  font-size: 14px;
  text-align: left;
  font-family: "Crimson Text", serif;
}

.contact__btn {
  width: 100%;
  height: 50px;
  color: #3CFD5B;
  background: transparent;
  border: 1px solid #e0e0e0;
  font-size: 24px;
  text-transform: uppercase;
}
.contact__btn:hover {
  background-color: #3CFD5B;
  color: #000000;
  border: 1px solid #3CFD5B;
}
@media (max-width: 991px) {
  .contact__btn {
    width: 100%;
    margin: 0 auto;
  }
}

.footer {
  margin-top: auto;
}

.footer__inner {
  border: 1px #fff solid;
  margin-top: -1px;
  padding: 20px 30px;
}
.footer__inner .nav__list {
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__inner .nav__list {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
.footer__inner .nav__item:not(:last-child) {
  margin-right: 0;
}
@media (max-width: 767px) {
  .footer__inner .nav__item:nth-child(2) {
    order: 1;
    flex-grow: 1;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .footer__inner .nav__item:nth-child(1) {
    order: 3;
  }
}
@media (max-width: 767px) {
  .footer__inner .nav__item:nth-child(3) {
    order: 2;
  }
}